import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CanDeactivateGuard } from 'src/app/shared/guards/can-deactivate.guard';
import { AddSystemSummaryComponent } from '../add-system-summary/add-system-summary.component';
import { AddSystemComponent } from './add-system.component';

const routes: Routes = [
  {
    path: '',
    component: AddSystemComponent,
    canDeactivate: [CanDeactivateGuard]
  },
  {
    path: 'summary',
    component: AddSystemSummaryComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddSystemRoutingModule { }



