  fetchDatabaseTable() {
    this.messageConfigurationForm.value.sysChannel[0].messageType=this.basicInfoForm.value.messsageType
    console.log(this.messageConfigurationForm.value.sysChannel[0]);
    this.addSystem
      .pingDataSource(
        this.messageConfigurationForm.value.sysChannel[0].dbConnectString,
        this.messageConfigurationForm.value.sysChannel[0].dbServiceName,
        this.messageConfigurationForm.value.sysChannel[0].dbUsername,
        this.messageConfigurationForm.value.sysChannel[0].dbPassword
      )
      .subscribe((resp) => {
        console.log("resp from backend for fetching database tables ", resp);
        this.table = resp;
      });

    if (this.index !== undefined) {
      for (
        let index = 0;
        index < this.tcpForm.value.sysService.length;
        index++
      ) {
        const filteredHomes =
          this.tcpForm.value.sysService[index].messageKey.split(",");
        this.tcpForm
          .get("sysService")
          ["controls"][index].get("messageKey")
          .setValue(filteredHomes[1]);
      }
    }
    this.label2 = "Column Name";
  }
